  padding: 0;
  position: absolute;
  cursor: pointer;
  box-shadow: none;
  border: none;
}

/* CURRENT COLOR COMPONENT */
.components-color-picker__swatch {
  margin-right: 8px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(45deg, #ddd 25%, transparent 25%), linear-gradient(-45deg, #ddd 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #ddd 75%), linear-gradient(-45deg, transparent 75%, #ddd 75%);
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0;
}
.is-alpha-disabled .components-color-picker__swatch {
  width: 12px;
  height: 12px;
  margin-top: 0;
}

.components-color-picker__active {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  z-index: 2;
}

/* SATURATION COMPONENT */
.components-color-picker__saturation-color,
.components-color-picker__saturation-white,
.components-color-picker__saturation-black {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.components-color-picker__saturation-color {
  overflow: visible;
}

.components-color-picker__saturation-white {
  /*rtl:ignore*/
  background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
}

.components-color-picker__saturation-black {
  background: linear-gradient(to top, #000, rgba(0, 0, 0, 0));
}

.components-button.components-color-picker__saturation-pointer {
  width: 14px;
  height: 14px;
  padding: 0;
  border-radius: 50%;
  background-color: transparent;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 0 1px #fff, inset 0 0 0 1px #000, 0 0 0 2px #000;
}
.components-button.components-color-picker__saturation-pointer:focus:not(:disabled) {
  box-shadow: 0 0 0 2px #fff, inset 0 0 0 1px #000, 0 0 0 3px #000;
}

/* HUE & ALPHA BARS */
.components-color-picker__toggles {
  flex: 1;
}

.components-color-picker__alpha {
  background-image: linear-gradient(45deg, #ddd 25%, transparent 25%), linear-gradient(-45deg, #ddd 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #ddd 75%), linear-gradient(-45deg, transparent 75%, #ddd 75%);
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0;
}

.components-color-picker__hue-gradient,
.components-color-picker__alpha-gradient {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.components-color-picker__hue,
.components-color-picker__alpha {
  height: 12px;
